<template>
    <div>
        <h1>自定义指令</h1>
        <h6>全局自定义指令v-focus</h6>
        <!--<input v-focus type="text">-->
        <h6>局部自定义指令v-focus2</h6>
        <input v-focus2:params.stop.prevent="instructionValue" type="text">
    </div>
</template>

<script>

export default {
  name: '自定义指令测试',
  data () {
    return {
      instructionValue: '我是绑定值'
    }
  },
  directives: {
    focus2: {
      /*
        * 自定义指令钩子函数 bind inserted update componentUpdate unbind
        * bind: 只调用一次，指令第一次绑定到元素时调用。在这里可以进行一次性的初始化设置。
        * inserted:  被绑定元素插入父节点时调用 (此时仅保证父节点存在，但父节点不一定已被插入文档中)。
        * update: 所在组件的 VNode 更新时调用 但是可能发生在其子 VNode 更新之前。 详情看文档
        * componentUpdate: 指令所在组件的 VNode 及其子 VNode 全部更新后调用。
        * unbind: 只调用一次，指令与元素解绑时调用。
        *
        * 指令钩子函数的参数 (即 el、binding、vnode 和 oldVnode)。
        * 除了 el 之外，其它参数都应该是只读的，切勿进行修改。
        * 如果需要在钩子之间共享数据，建议通过元素的 dataset 来进行。
        *
        * el：指令所绑定的元素，可以用来直接操作 DOM 。
        * 实例
        * <input data-v-1bc50460="" type="text">
        *
        * binding：一个对象，包含以下属性：
        * name：指令名，不包括 v- 前缀
        * value：指令的绑定值，例如：v-my-directive="1 + 1" 中，绑定值为 2。也可以是个对象
        * expression：字符串形式的指令表达式。例如 v-my-directive="1 + 1" 中，表达式为 "1 + 1"。
        * arg：传给指令的参数，可选。例如 v-my-directive:foo 中，参数为 "foo"。
        * modifiers：一个包含修饰符的对象。例如：v-my-directive.foo.bar 中，修饰符对象为 { foo: true, bar: true }
        * oldValue：指令绑定的前一个值，仅在 update 和 componentUpdated 钩子中可用。无论值是否改变都可用。
        * 实例
        * {
        *     "name":"focus2",
        *     "rawName":"v-focus2:params",
        *     "value":"我是绑定值",
        *     "expression":"instructionValue",
        *     "modifiers":{"stop":true,"prevent":true},
        *     "arg":"params",
        *     "def":{}
        * }
        *
        * vnode：Vue 编译生成的虚拟节点。
        * 实例
        * VNode {tag: "input", data: {…}, children: undefined, text: undefined, elm: input, …}
        *
        * oldVnode：上一个虚拟节点，仅在 update 和 componentUpdated 钩子中可用。
        * */
      bind (el, binding, vnode) {
        console.log(el)
        console.log(JSON.stringify(binding))
        console.log(vnode)
      },
      inserted: function (el) {
        el.focus()
      }
    }
  }

}
</script>

<style scoped>

</style>



